.elements {
  padding-top: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  grid-template-rows: max-content;
  column-gap: 17px;
  row-gap: 20px;
  justify-content: space-evenly;
}

@media screen and (max-width: 550px) {
  .elements {
    padding-top: 36px;
  }
}
